import React, { Component } from 'react';
import axios from 'axios';
import './loader.css';
import { MDBDataTable } from 'mdbreact';
import ToggleFavourite from './add-fav';
import {
  CPagination,
  CSelect,
  CRow,
  CCol,
  CCard,
  CCardBody,
  CButton,
  CSwitch,
  CInput,
  CInputGroup,
} from '@coreui/react';
const LOADER = <div className='loader p-2 ml-5 d-flex'></div>;
const columns = [
  {
    label: 'Id',
    field: 'id',
    sort: 'asc',
  },
  {
    label: 'Domain',
    field: 'domain',
    sort: 'asc',
  },
  {
    label: 'Metrics',
    field: 'metrics',
    sort: 'asc',
  },
  {
    label: 'Words',
    field: 'words',
    sort: 'asc',
  },
  {
    label: 'Sentiment_score',
    field: 'sentiment_score',
    sort: 'asc',
  },
  {
    label: 'Add_Fav',
    field: 'add_fav',
    sort: 'asc',
  },
];

export default class CustomTables extends Component {
  state = {
    isloading: false,
    is_favourite: true,
    pageNo: 1,
    size: 5,
    pagesAmount: 1,
    columns: [...columns],
    rows: [],
    domain: null,
    length: null,
    alphanumeric: 0,
    numeric: null,
    hyphenated: 0,
    tld: null,
    wordcount: null,
    words: null,
    score: null,
    countselect: '0',
    scoreselect: '0',
    lengthselect: '0',
  };
  onPageChange = (e) => {
    this.setState({ pageNo: e }, () => this.getData(this.state));
  };
  onChange = (e) => {
    const { name, value } = e.target;
    if (name == 'hyphenated' || name == 'alphanumeric' || name == 'numeric') {
      let ans = () => (e.target.checked ? 1 : 0);
      this.setState({
        [name]: ans(),
      });
    } else {
      this.setState({
        [name]: value.trim(),
      });
    }
  };
  onClickSearch = () => {
    this.getData(this.state);
  };
  getData = async (args) => {
    this.setState({ isloading: true });
    await axios
      .post('api/get_domains', {
        ...args,
      })
      .then((res) => {
        console.log(res);
        let data = res.data.data;
        let pages = res.data.pages;
        let rows = [];
        if (res.data.data.length > 0) {
          data.map((data) => {
            rows.push({
              id: data._id,
              domain: data.domain,
              metrics: (
                <>
                  <span>alphanumeric : {data.metrics.alphanumeric}</span>
                  <hr className='p-0 m-0' />
                  <span>hyphenated :{data.metrics.hyphenated}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    length:
                    {data.metrics.length}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>numeric : {data.metrics.numeric}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    tld:
                    {data.metrics.tld}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>wordcount : {data.metrics.wordcount}</span>
                  <hr className='p-0 m-0' />
                </>
              ),
              words: (
                <>
                  {data.words.map((w, key) => {
                    return (
                      <span key={key}>
                        , {w} <hr className='p-0 m-0' />
                      </span>
                    );
                  })}
                </>
              ),
              sentiment_score: data.sentiment_score,
              add_fav: (
                <button
                  className={
                    !data.is_favourite
                      ? 'btn btn-success btn-sm'
                      : 'btn btn-danger btn-sm'
                  }
                  onClick={() => {
                    ToggleFavourite(data._id, !data.is_favourite);
                    this.getData(this.state);
                  }}>
                  {!data.is_favourite ? <> add fav</> : <>remove fav</>}
                </button>
              ),
            });
          });
          console.log('Setting state . .. ', rows);
          this.setState({
            ...this.state,
            rows,
            pagesAmount: pages,
            isloading: false,
          });
        } else {
          this.setState({
            ...this.state,
            isloading: false,
            rows: [],
            pageNo: 1,
            pagesAmount: 1,
          });
        }
      })
      .catch((err) => {
        console.log(err);
        this.setState({
          ...this.state,
          rows: [],
          pageNo: 1,
          pagesAmount: 1,
          isloading: false,
        });
      });
  };
  render() {
    return (
      <>
        <header className='text-align-center'>
          <h1>Favourite COMPONENT</h1>
        </header>
        <CRow className='m-0 p-0'>
          <CCol xs='3' sm='2' md='1' className='m-1 p-0 '>
            <CButton
              block
              variant='outline'
              color='success'
              onClick={this.onClickSearch}>
              Search
            </CButton>
          </CCol>
          <CCol
            xs='2'
            sm='1'
            md='1'
            className='m-1 p-0 rounded border border-success'>
            <CSelect
              custom
              name='size'
              placeholder='No of Entries'
              onChange={this.onChange}
              id='ccmonth'>
              <option value='5'>5</option>
              <option value='10'>10</option>
              <option value='20'>20</option>
              <option value='30'>30</option>
              <option value='40'>40</option>
              <option value='50'>50</option>
              <option value='60'>60</option>
              <option value='70'>70</option>
              <option value='80'>80</option>
              <option value='90'>90</option>
              <option value='100'>100</option>
              <option value='500'>500</option>
              <option value='1000'>1000</option>
            </CSelect>
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='2'
            className='m-1 p-0 rounded border border-success'>
            <input
              style={{ height: '35px' }}
              placeholder='Domain'
              name='domain'
              onChange={this.onChange}
              type='text'
              className='form-control form-control-sm'
            />
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='2'
            className='m-1 p-0 rounded border border-success'>
            <input
              style={{ height: '35px' }}
              placeholder='Words'
              name='words'
              onChange={this.onChange}
              type='text'
              className='form-control form-control-sm'
            />
          </CCol>

          <CCol
            xs='5'
            sm='4'
            md='2'
            style={{ height: '35px' }}
            className='m-1 p-0 rounded border border-success'>
            <div className='form-check'>
              <label
                style={{ marginLeft: '-18px' }}
                className='form-check-label mt-2 '>
                alphanumeric
              </label>
              <CSwitch
                // size={'lg'}
                shape={'pill'}
                name={'alphanumeric'}
                className={'mx-1 p-auto form-check-input'}
                onChange={this.onChange}
                color={'success'}
                labelOn={'Yes'}
                labelOff={'No'}
              />
            </div>
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='2'
            style={{ height: '35px' }}
            className='m-1 p-0 rounded border border-success'>
            <div className='form-check'>
              <label className='form-check-label mt-2 '>hyphenated</label>
              <CSwitch
                // size={'lg'}
                shape={'pill'}
                name={'hyphenated'}
                className={'mx-1 p-auto form-check-input'}
                onChange={this.onChange}
                color={'success'}
                labelOn={'Yes'}
                labelOff={'No'}
              />
            </div>
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='2'
            style={{ height: '35px' }}
            className='m-1 p-0 rounded border border-success'>
            <div className='form-check'>
              <label className='form-check-label mt-2 '>numeric</label>
              <CSwitch
                // size={'lg'}
                shape={'pill'}
                name={'numeric'}
                className={'mx-1 p-auto form-check-input'}
                onChange={this.onChange}
                color={'success'}
                labelOn={'Yes'}
                labelOff={'No'}
              />
            </div>
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='1'
            className='m-1 p-0 rounded border border-success'>
            <input
              style={{ height: '35px' }}
              placeholder='tld'
              name='tld'
              onChange={this.onChange}
              type='text'
              className='form-control form-control-sm'
            />
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='3'
            className='m-1 p-0 rounded border border-success'>
            <CInputGroup>
              <CInput
                style={{ height: '35px' }}
                name='wordcount'
                placeholder='wordcount'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
              <CCol
                xs='5'
                sm='4'
                md='3'
                style={{ marginLeft: '-2px' }}
                className='m-0 p-0'>
                <CSelect
                  onChange={this.onChange}
                  name='countselect'
                  style={{ height: '35px' }}
                  custom
                  id='select'
                  size='sm'>
                  <option value='0' selected>
                    eq
                  </option>
                  <option value='1'>(gt)</option>
                  <option value='2'>(lt)</option>
                  <option value='3'>(gte)</option>
                  <option value='4'>(lte)</option>
                </CSelect>
              </CCol>
            </CInputGroup>
          </CCol>

          <CCol
            xs='5'
            sm='4'
            md='3'
            className='m-1 p-0 rounded border border-success'>
            <CInputGroup>
              <CInput
                style={{ height: '35px' }}
                placeholder='length'
                name='length'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
              <CCol
                xs='5'
                sm='4'
                md='3'
                style={{ marginLeft: '-2px' }}
                className='m-0 p-0'>
                <CSelect
                  style={{ height: '35px' }}
                  custom
                  onChange={this.onChange}
                  name='lengthselect'
                  id='select'
                  size='sm'>
                  <option value='0' selected>
                    eq
                  </option>
                  <option value='1'>(gt)</option>
                  <option value='2'>(lt)</option>
                  <option value='3'>(gte)</option>
                  <option value='4'>(lte)</option>
                </CSelect>
              </CCol>
            </CInputGroup>
          </CCol>
          <CCol
            xs='5'
            sm='4'
            md='3'
            className='m-1 p-0 rounded border border-success'>
            <CInputGroup>
              <CInput
                style={{ height: '35px' }}
                placeholder='Sentimental score'
                name='score'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
              <CCol
                xs='5'
                sm='4'
                md='3'
                style={{ marginLeft: '-2px' }}
                className='m-0 p-0'>
                <CSelect
                  style={{ height: '35px' }}
                  custom
                  onChange={this.onChange}
                  name='scoreselect'
                  id='select'
                  size='sm'>
                  <option value='0' selected>
                    eq
                  </option>
                  <option value='1'>(gt)</option>
                  <option value='2'>(lt)</option>
                  <option value='3'>(gte)</option>
                  <option value='4'>(lte)</option>
                </CSelect>
              </CCol>
            </CInputGroup>
          </CCol>
        </CRow>

        <CRow>
          <CCol className='m-0 p-0'>
            <CCard className='m-0 p-0'>
              <CCardBody className='heavy-rain-gradient m-0 p-0'>
                {this.state.isloading ? (
                  LOADER
                ) : (
                  <MDBDataTable
                    className='text-dark black-text p-2 strong'
                    searching={false}
                    responsive
                    bordered={true}
                    paging={false}
                    striped
                    theadColor='dark'
                    small
                    noBottomColumns
                    hover
                    data={this.state}
                  />
                )}
                <CPagination
                  align='center'
                  activePage={this.state.pageNo}
                  pages={this.state.pagesAmount}
                  onActivePageChange={this.onPageChange}
                />
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}
